import { Component, OnInit } from '@angular/core';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';

@Component({
  selector: 'app-assets-setup-depreciation',
  templateUrl: './assets-setup-depreciation.component.html',
  styleUrls: ['./assets-setup-depreciation.component.scss']
})
export class AssetsSetupDepreciationComponent implements OnInit {

  isDataLoaded:boolean = false;
  isError:boolean = false;

  depreciatonData:any;

  message:any;

  constructor(
    private lookupService: LookupService,
		private sharedService: SharedService,
		private sessionService: SessionService,
  ) { 

    this.message = null

  }

  getDepreciatonList() {

    this.isDataLoaded = false

		let queryParamBase = {
			ApartmentId: this.sessionService.apartmentId,
			LookupTypeId: 104,
		};		
		this.lookupService.getLookupValueByLookupTypeId(queryParamBase).subscribe((res: any) => {
			if (res) {

        this.isDataLoaded = true

        this.depreciatonData = res?res:[];
        if(this.depreciatonData && this.depreciatonData.length){
              this.depreciatonData.filter( val =>{
                val.isCheck = val.isDisabled == true ? false:true;
              })
            }
        }
		});
  }

  updateDepreciatonList(data) {
    
    this.isDataLoaded = false;

    let reqObj = {
      "lookupValueId":data.lookupValueId,
      "lookupTypeId": 104,
      "lookupValueName": data.lookupValueName,
      "description": data.description,
      'apartmentId': this.sessionService.apartmentId,
      "isActive": true,
      "insertedBy": parseInt(this.sessionService.userId),
      "insertedOn": new Date(),
      "updatedBy": parseInt(this.sessionService.userId),
      "updatedOn": new Date(),
      "isDisabled": !data.isCheck
    };

    console.log(reqObj)
    
    let param :any = {
      lookupvalue: reqObj
    };
    this.lookupService.updateLookupValue(param).subscribe((res: any) => {
      if (res) {
        this.sharedService.openSnackBar("Depreciaton method  updated successfully",'success');
        this.isDataLoaded = true;
        this.getDepreciatonList();
      } else if (res.body.errorMessage) {
        this.isDataLoaded = true;
      }
    });
  }

  ngOnInit(): void {

    this.getDepreciatonList();

  }

}
